*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

input[type=number] {
  -moz-appearance: textfield;
}

div {
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

button,
input {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
}

button {
  padding: 0;
  margin: 0;
  border: none !important;
  outline: none !important;
}

a {
  padding: 0;
  margin: 0;
  color: inherit;
  text-decoration: none;
}

img {
  display: flex;
  object-fit: cover;
}

* {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.1875rem;
  background-color: white;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  padding-top: 13.5625rem;
  overflow: hidden;
}

.container {
  max-width: 89.6875rem;
  padding: 0 0.9375rem;
  margin: 0 auto;
}

.title-wrapper {
  position: relative;
  padding-top: 6.25rem;
  text-align: center;
}
.title-wrapper__main {
  font-size: 3rem;
  line-height: 3rem;
  color: #36414d;
  text-transform: uppercase;
  font-family: "Bebas_Neue_Cyrillic", sans-serif;
  font-weight: 400;
}
.title-wrapper__text {
  margin-top: 1.875rem;
}

.btn {
  display: inline-block;
  min-width: 20.125rem;
  padding: 0.75rem;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 1rem;
  transition: all 0.4s ease-in-out;
}
.btn .btn__text {
  font-size: 1.5625rem;
  line-height: 1.8125rem;
  text-align: center;
  white-space: nowrap;
}
.btn--transparent {
  border: 0.0625rem solid #f95f55;
}
.btn--transparent .btn__text {
  color: #f95f55;
  transition: all 0.4s ease-in-out;
}
.btn--transparent:hover {
  background-color: #f95f55;
}
.btn--transparent:hover .btn__text {
  color: #fbfbfb;
}
.btn--orange {
  position: relative;
}
.btn--orange .btn__text {
  position: relative;
  z-index: 1;
  color: #fbfbfb;
}
.btn--orange::before, .btn--orange::after {
  position: absolute;
  top: 50%;
  right: 50%;
  bottom: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  content: "";
  border-radius: 1rem;
  transition: all 0.4s ease-in-out;
  transform: translate(-50%, -50%);
}
.btn--orange::after {
  background: #fa6926;
  opacity: 1;
}
.btn--orange::before {
  background: linear-gradient(270deg, #6d4fbf 0%, #52417e 100%);
  opacity: 0;
}
.btn--orange:hover::before {
  opacity: 1;
}
.btn--orange:hover::after {
  opacity: 0;
}
.btn--red {
  background: #f95f55;
  transition: all 0.4s ease-in-out;
}
.btn--red .btn__text {
  color: #fbfbfb;
}
.btn--red:hover {
  background-color: #6d4fbf;
}

.logo {
  display: flex;
  text-transform: uppercase;
}
.logo__photo {
  width: 3.8125rem;
  height: 3.1875rem;
}
.logo__photo img {
  width: 100%;
  height: 100%;
}
.logo__content {
  margin-left: 1.4375rem;
  white-space: nowrap;
}
.logo__content-bottom {
  font-weight: 700;
}
.logo__content span {
  display: block;
}
.logo__content span + span {
  margin-top: 0.4375rem;
}

.social {
  margin-left: 1.6875rem;
}
.social__items {
  display: flex;
}
.social__item {
  width: 2rem;
  height: 2rem;
}
.social__item + .social__item {
  margin-left: 0.9375rem;
}
.social__item img {
  width: 100%;
  height: 100%;
}

.product-card {
  width: 21.5rem;
  height: 27.25rem;
  -webkit-perspective: 1000;
  -moz-perspective: 1000;
  -ms-perspective: 1000;
  perspective: 1000;
  -ms-transform: perspective(62.5rem);
  -moz-transform: perspective(62.5rem);
  -moz-transform-style: preserve-3d;
  -ms-transform-style: preserve-3d;
  transform-style: preserve-3d;
}
.product-card__img {
  max-width: 16.375rem;
  max-height: 12.5rem;
  margin: auto auto 0;
}
.product-card__img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.product-card__content {
  margin-top: 4.5rem;
  font-size: 1.25rem;
  line-height: 1.4375rem;
  color: #36414d;
}
.product-card__content-title {
  margin-top: 0.9375rem;
}
.product-card__content-price {
  position: relative;
  padding-right: 1.5625rem;
  margin-top: 0.1875rem;
}
.product-card__content-price::before {
  position: absolute;
  top: 35%;
  right: 0;
  bottom: 50%;
  font-size: 0.625rem;
  line-height: 0.75rem;
  color: #36414d;
  content: "UAH";
  transform: translateY(-50%);
}
.product-card__back {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 21.5rem;
  height: 27.25rem;
  padding: 1rem;
  background-color: white;
  transform: rotateY(-180deg);
}
.product-card__back-button {
  min-width: 100%;
  background-color: #fa6926;
}
.product-card__back-button .btn__text {
  color: #fbfbfb;
}
.product-card__front {
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 21.5rem;
  height: 27.25rem;
  padding: 1rem;
  background-color: white;
  transform: rotateY(0deg);
}
.product-card__back-description {
  margin: 0.8125rem 0 auto 0;
  font-size: 1.25rem;
  line-height: 1.4375rem;
  color: #36414d;
}
.product-card__back-button {
  margin-top: 43px;
}

.product-card__type {
  padding-bottom: 0.5625rem;
  font-size: 1.25rem;
  line-height: 1.25rem;
  color: #adbfbf;
  border-bottom: 1px solid #e6e6e6;
  font-family: "Bebas_Neue_Cyrillic", sans-serif;
  font-weight: 400;
}

.product-card__flip {
  position: relative;
  transition: 0.4s;
  transform-style: preserve-3d;
}

.product-card__front,
.product-card__back {
  position: absolute;
  top: 0;
  left: 0;
  backface-visibility: hidden;
  transition: 0.6s;
  transform-style: preserve-3d;
}

.product-card:hover .product-card__back {
  transform: rotateY(0deg);
}
.product-card:hover .product-card__front {
  transform: rotateY(-180deg);
}

.text {
  font-size: 20px;
  line-height: 35px;
  color: #707070;
}

.pagination {
  display: flex;
  justify-content: center;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  color: #d9d9d9;
}
.pagination__title {
  margin-right: 0.9375rem;
}
.pagination__item {
  color: #6d4fbf;
}
.pagination__item-current {
  color: #d9d9d9;
}
.pagination__item + .pagination__item {
  margin-left: 0.3125rem;
}

.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 999;
  background-color: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(11.5px);
}

.header-top {
  z-index: 99999;
  border-bottom: 0.0625rem solid #c4c4c4;
}
.header-top__menu {
  max-width: 58.75rem;
  padding: 0 0.9375rem;
  margin: 0 auto;
}

.top-menu__list {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2.3125rem;
  text-transform: uppercase;
}
.top-menu__list-item {
  font-size: 0.875rem;
  line-height: 1rem;
}
.top-menu__list-item + .top-menu__list-item {
  margin-left: 4.375rem;
}
.top-menu__list-item-link {
  transition: all 0.4s ease-in-out;
}
.top-menu__list-item-link:hover {
  color: gray;
}

.header-center {
  z-index: 9999;
  display: flex;
  align-items: center;
  max-width: 106.75rem;
  height: 7.0625rem;
  padding: 0 0.9375rem;
  margin: 0 auto;
}
.header-center__lang {
  margin: 0 2.5625rem 0 6.0625rem;
}

.header-search {
  margin-left: auto;
}
.header-search__button {
  width: 2rem;
  height: 2rem;
  margin-left: auto;
  cursor: pointer;
  background: url("data:image/svg+xml;charset=UTF-8,%3csvg width='25' height='24' viewBox='0 0 25 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M24.0889 22.2056L18.4027 16.8577C20.2569 15.0556 21.4313 12.6142 21.4313 9.88208C21.4313 4.41787 16.7339 0 10.9241 0C5.1143 0 0.416992 4.41787 0.416992 9.88208C0.416992 15.3463 5.1143 19.7642 10.9241 19.7642C13.1492 19.7642 15.2506 19.0666 16.9812 17.9621L22.791 23.4263C22.9764 23.6007 23.2236 23.6589 23.4709 23.6589C23.7181 23.6589 23.9653 23.6007 24.1507 23.4263C24.4598 23.0776 24.4598 22.5544 24.0889 22.2056ZM2.27119 9.88208C2.27119 5.40608 6.16501 1.7439 10.9241 1.7439C15.6832 1.7439 19.5771 5.40608 19.5771 9.88208C19.5771 14.3581 15.6832 18.0203 10.9241 18.0203C6.16501 18.0203 2.27119 14.3581 2.27119 9.88208Z' fill='%232F3035'/%3e%3c/svg%3e ") no-repeat center center;
  opacity: 30%;
}
.header-search__input {
  width: 0%;
  border-bottom: 0.0625rem solid #c4c4c4;
  transition: all 0.4s ease-in-out;
}
.header-search__form {
  display: flex;
}
.header-search__form--active .header-search__input {
  width: 85%;
}

.lang__items {
  display: flex;
}
.lang__item {
  font-size: 1.5rem;
  line-height: 1.75rem;
  cursor: pointer;
  opacity: 0.2;
  transition: all 0.4s ease-in-out;
}
.lang__item + .lang__item {
  margin-left: 1.5rem;
}
.lang__item--active {
  opacity: 1;
}
.lang__item:hover {
  opacity: 0.6;
}

.header-bottom__menu {
  background-color: #00c1b6;
}

.menu {
  max-width: 68.25rem;
  padding: 0 0.9375rem;
  margin: 0 auto;
}
.menu__list {
  display: flex;
  align-items: center;
  height: 66px;
}
.menu__list-item {
  position: relative;
  padding: 0 1.25rem;
  color: #fbfbfb;
  text-align: center;
  text-transform: uppercase;
}
.menu__list-item::before {
  position: absolute;
  top: 50%;
  right: 0;
  bottom: 50%;
  width: 0.0625rem;
  height: 3.125rem;
  content: "";
  background-color: #fbfbfb;
  transform: translateY(-50%);
}
.menu__list-item:last-child::before {
  display: none;
}
.menu__list-item-link {
  transition: all 0.4s ease-in-out;
}
.menu__list-item-link:hover {
  color: #d5d5d5;
}

.dropdown {
  position: absolute;
  top: -1000%;
  right: 0;
  left: 0;
  z-index: -1;
  max-height: 66vh;
  overflow: auto;
  background-color: #fbfbfb;
  opacity: 0;
  transition: all 0.5s ease-in-out;
}
.dropdown__items {
  column-count: 4;
  max-width: 72.5rem;
  padding: 4.375rem 1.875rem;
  margin: 0 auto;
}

.dropdown__item {
  max-width: 13.4375rem;
  margin-bottom: 1.75rem;
}
.dropdown__item-title {
  font-size: 1.25rem;
  line-height: 1.25rem;
  font-family: "Bebas_Neue_Cyrillic", sans-serif;
  font-weight: 400;
}
.dropdown__item-point {
  margin-top: 0.375rem;
}
.dropdown__item-point + .dropdown__item-point {
  margin-top: 0.75rem;
}
.dropdown__item-link {
  font-size: 0.875rem;
  line-height: 1rem;
  color: #36414d;
}

.header-catalog__dropdown--active,
.header-cleansing__dropdown--active {
  top: 13.5625rem;
  opacity: 1;
}

.header-cleansing .dropdown__items {
  column-count: auto;
}
.header-cleansing .dropdown__item-point {
  list-style: auto;
}

.footer {
  color: #fbfbfb;
  background-color: #36414d;
}

.footer__bottom {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 4.125rem 0 3.8125rem 0;
  font-size: 0.875rem;
  line-height: 1rem;
}
.footer__bottom::before {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 0.0625rem;
  content: "";
  background-color: #c4c4c4;
}
.footer__bottom-text {
  opacity: 0.5;
}

.footer__menu {
  padding: 4.125rem 0 2.3125rem 0;
}
.footer__menu-items {
  display: flex;
  justify-content: space-between;
}
.footer__menu-point + .footer__menu-point {
  margin-top: 0.875rem;
}
.footer__menu-title {
  font-size: 1.5rem;
  line-height: 1.5rem;
  font-family: "Bebas_Neue_Cyrillic", sans-serif;
  font-weight: 400;
}
.footer__menu-link {
  transition: all 0.4s ease-in-out;
}
.footer__menu-link:hover {
  color: #bbbbbb;
}

.intro__slider-img {
  width: 100%;
}

.about .title-wrapper__text {
  width: 100%;
  max-width: 15.9375rem;
  margin: 1.875rem auto 0;
}
.about__content {
  background: url("../images/content/map.webp") top center/contain no-repeat;
}
.about__items {
  padding: 6.875rem 0 10.625rem;
  text-align: -webkit-center;
  column-count: 3;
}

.about__item {
  max-width: 21.875rem;
  min-height: 15rem;
  margin-bottom: 5.625rem;
  text-align: center;
}
.about__item:nth-of-type(3n) {
  margin-right: 0;
}
.about__item-number {
  font-size: 10rem;
  line-height: 10rem;
  font-family: "Bebas_Neue_Cyrillic", sans-serif;
  font-weight: 400;
}
.about__item-number--blue {
  color: #4b6387;
}
.about__item-number--dark-green {
  color: #328c4f;
}
.about__item-number--light-red {
  color: #dc554c;
}
.about__item-number--light-green {
  color: #91c164;
}
.about__item-number--dark-red {
  color: #a5121e;
}
.about__item-number--purple {
  color: #6d4fbf;
}
.about__item-text {
  font-size: 2.25rem;
  line-height: 2.625rem;
}

.banner {
  height: 25.75rem;
}
.banner__items {
  display: flex;
  overflow: hidden;
}
.banner__item {
  position: relative;
  width: 34%;
  height: 25.75rem;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  transition: background-size 0.5s ease;
}
.banner__item:hover {
  background-size: 108% 108%;
}
.banner__item-title {
  position: absolute;
  top: 50%;
  right: 50%;
  bottom: 50%;
  left: 50%;
  display: inline-table;
  padding: 0.9375rem 1rem 0.6875rem 1.0625rem;
  font-size: 2rem;
  line-height: 2rem;
  color: #fbfbfb;
  white-space: nowrap;
  transform: translate(-50%, -50%);
  font-family: "Bebas_Neue_Cyrillic", sans-serif;
  font-weight: 400;
}
.banner__item-title--green {
  background-color: #328c4f;
}
.banner__item-title--purple {
  background-color: #6d4fbf;
}
.banner__item-title--orange {
  background-color: #ef4718;
}

.sale {
  padding: 0 0 6.25rem;
  background-color: #fbfbfb;
}
.sale__top {
  margin-bottom: 8.125rem;
}
.sale__items {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(4, 1fr);
  grid-row-gap: 2.1875rem;
  grid-column-gap: 1.25rem;
}

.join {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 36.875rem;
  background: url("../images/content/join.webp") center center/cover no-repeat;
}

.join__button {
  min-width: 40rem;
  padding: 2.25rem;
