// === FOOTER ===
.footer {
    color: $white;
    background-color: $dark;
}

.footer__bottom {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: rem(66px) 0 rem(61px) 0;
    font-size: $fs-xxs;
    line-height: rem(16px);

    &::before {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        width: 100%;
        height: rem(1px);
        content: '';
        background-color: $gray;
    }

    &-text {
        opacity: 0.5;
    }
}

.footer__menu {
    padding: rem(66px) 0 rem(37px) 0;

    &-items {
        display: flex;
        justify-content: space-between;
    }

    &-point + &-point {
        margin-top: rem(14px);
    }

    &-title {
        font-size: rem(24px);
        line-height: rem(24px);
        @include Bebas_Neue_Cyrillic($weight: 400);
    }

    &-link {
        transition: $transition;

        &:hover {
            color: darken($white, 25%);
        }
    }
}
// === / FOOTER ===
